@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.work {
  width: 400px;
  background-color: $bgSecondColor;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: start;
  margin: 20px 0;

  .workImage {
    width: 100%;
    height: 280px;
    background-color: #eeeef5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
    }

    .buttonWork {
      width: 90px;
      height: 50px;
      background-color: $primaryColor;
      position: relative;
      z-index: 1;
      color: $textColor;
    }
  }

  .workInfo {
    padding: 25px 20px;
    background-color: $bgSecondColor;

    .nameProject {
      @include text($titleFontFamilyPoppins, 16px, 1.2);
      letter-spacing: 1px;
      margin: 0 0 5px;
    }

    .descriptionProject {
      width: 100%;
      @include text($textFontFamilyJosefinSans, 14px, 1.8);
      font-weight: 400;
    }
  }
}












